.main-comments{
    display: flex;
    flex-direction: column;
    grid-gap: clamp(30px, 2.667vw, 40px);

    --gap: clamp(47px, 5.267vw, 79px);

    &__title{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: clamp(20px, 1.933vw, 29px);
        line-height: 150%;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: rgba(var(--cl-c),1.0);
    }

    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__date{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: clamp(14px, 1.2vw, 18px);
        line-height: 140%;
        color: rgba(var(--cl-g),1.0);
        
    }

    &__list{
        display: flex;
        flex-direction: column;
        grid-gap: var(--gap);
        padding-bottom: calc((var(--gap) / 2));
    }

    &__item{
        display: flex;
        flex-direction: column;
        grid-gap: 20px;
        position: relative;

        &::before{
            content: '';
            position: absolute;
            width: 100%;
            height: 1px;
            left: 0;
            bottom: calc((var(--gap) / 2) * -1);
            background-color: rgba(var(--bg-e),1.0);
        }

        @include mobile{
            grid-gap: 23px;
        }
    }

    &__user{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size:  clamp(16px, 1.2vw, 18px);
        line-height: 140%;
        color: rgba(var(--cl-c),1.0);
        
    }

    p{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: rgba(var(--cl-b),1.0);

    }
}